/* Quiz 컨테이너 */
.quiz-container {
    font-family: Arial, sans-serif;
    text-align: center;
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
    border: 2px solid #ddd;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* 퀴즈 제목 */
.quiz-container h1 {
    color: #333;
    font-size: 24px;
    margin-bottom: 20px;
}

/* 질문 스타일 */
.question {
    font-size: 18px;
    color: #555;
    margin-bottom: 20px;
}

/* 옵션 버튼 컨테이너 */
.options-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
}

/* 옵션 버튼 기본 스타일 */
.option-button {
    background-color: #fff;
    border: 2px solid #007bff;
    border-radius: 5px;
    color: #007bff;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
}

/* 선택된 옵션 버튼 스타일 */
.option-button.selected {
    background-color: #007bff;
    color: #fff;
}

/* 옵션 버튼 hover 효과 */
.option-button:hover {
    background-color: #007bff;
    color: #fff;
}

/* 확인 버튼 */
.submit-button {
    background-color: #28a745;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 20px;
}

.submit-button:disabled {
    background-color: #ddd;
    cursor: not-allowed;
}

.submit-button:hover:enabled {
    background-color: #218838;
}

/* 퀴즈 완료 메시지 */
.quiz-finish {
    margin-top: 20px;
}

/* 다시 시작 버튼 */
.restart-button {
    background-color: #ffc107;
    color: #fff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.restart-button:hover {
    background-color: #e0a800;
}
/* 정답 입력창 스타일 */
.answer-input {
    width: 80%;
    padding: 10px;
    font-size: 16px;
    margin: 20px 0;
    border: 2px solid #ddd;
    border-radius: 5px;
    outline: none;
}

.answer-input:focus {
    border-color: #007bff;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Quiz 전체 컨테이너 스타일 */
.quiz-selection {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f0f8ff;
    font-family: Arial, sans-serif;
    color: #333;
    text-align: center;
}

/* 퀴즈 제목 스타일 */
.quiz-selection h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #2c3e50;
}

/* 버튼 스타일 */
.selection-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 15px 30px;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 8px;
    margin: 10px 0;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
    width: 250px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* 버튼 호버 효과 */
.selection-button:hover {
    background-color: #0056b3;
    transform: scale(1.05);
}

/* 버튼 클릭 효과 */
.selection-button:active {
    background-color: #004080;
    transform: scale(0.98);
}

/* 반응형 스타일 */
@media (max-width: 768px) {
    .quiz-selection h1 {
        font-size: 2rem;
    }

    .selection-button {
        font-size: 0.9rem;
        padding: 12px 25px;
        width: 200px;
    }
}
